/* Reset some default styles */
body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

/* Center the entire app vertically and horizontally */
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-color: #f8f8f8;
  text-align: center;
  color: #333;
}

/* Style the header */
.App-header {
  background-color: #282c34;
  padding: 20px;
  border-bottom: 1px solid #ddd;
  width: 100%;
  color: white;
}

/* Logo styles */
.App-logo {
  width: 150px;
  height: auto;
  animation: App-logo-spin infinite 20s linear;
}

/* Header title */
.App-title {
  font-size: 24px;
  margin: 10px 0;
}

/* Table container styles */
.table-container {
  margin-top: 20px;
  background-color: white;
  border-radius: 5px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  overflow: hidden;
}

/* Table styles */
table {
  width: 100%;
  border-collapse: collapse;
}

th, td {
  padding: 15px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

th {
  background-color: #f2f2f2;
  font-weight: bold;
}
.adam-rating{
  background-color: lightblue;
}
.kia-rating{
  background-color: pink;
}

/* Add to Database Form Styles */
.AddTable {
  margin-top: 20px;
  background-color: white;
  border-radius: 5px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  overflow: hidden;
  padding: 20px;
}

.movie-form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.form-input {
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  outline: none;
  font-size: 16px;
}

.submit-button {
  background-color: #007bff;
  color: #fff;
  padding: 12px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 18px;
  transition: background-color 0.3s ease;
}

.submit-button:hover {
  background-color: #0056b3;
}

/* Animation for the logo */
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
